import React, { useEffect, useRef, useState } from "react";
import { v4 as uuidv4 } from "uuid";

import { setSort } from "./redux/slices/filterSlice";
import { ESort } from "./redux/slices/filterSlice";
import { RootState, useAppDispatch } from "./redux/store";
import { useSelector } from "react-redux";
type TPopupClick = MouseEvent & {
  composedPath(): Node[];
};
type TListFunc<T> = (i: number) => T;

const Sort: React.FC = () => {
  const [open, setOpen] = useState(false);
  const [selected, setSelected] = useState(0);
  const sortRef = useRef<HTMLDivElement>(null);
  const dispatch = useAppDispatch();
  const { sort } = useSelector((state: RootState) => state.filter);
  const list = ["", "", ""];
  const listObj = {
    rating: "",
    price: "",
    title: "",
  };

  const listFunc: TListFunc<ESort> = (i) => {
    let resultType: ESort = ESort.RATING;
    switch (i) {
      case 0:
        resultType = ESort.RATING;
        break;
      case 1:
        resultType = ESort.PRICE;
        break;
      case 2:
        resultType = ESort.TITLE;
        break;
      default:
        return resultType;
    }

    return resultType;
  };

  const onClickListItem = (i: number) => {
    setSelected(i);
    setOpen((open) => !open);
    dispatch(setSort(listFunc(i)));
  };

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      const _event = event as TPopupClick;
      if (sortRef.current && !_event.composedPath().includes(sortRef.current)) {
        setOpen(false);
      }
    };
    document.body.addEventListener("click", handleClickOutside);

    return () => document.body.removeEventListener("click", handleClickOutside);
  }, []);

  return (
    <div ref={sortRef} className="sort">
      <div className="sort__label">
        <svg
          width="10"
          height="6"
          viewBox="0 0 10 6"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
            fill="#2C2C2C"
          />
        </svg>
        <b> :</b>
        <span onClick={() => setOpen((elem) => !elem)}>{listObj[sort]}</span>
      </div>
      {open && (
        <div className="sort__popup">
          <ul>
            {list.map((elem, i) => (
              <li
                key={uuidv4()}
                onClick={() => onClickListItem(i)}
                className={selected === i ? "active" : ""}
              >
                {elem}
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
};

export default Sort;
